<template lang="pug">
.login-container
    .login-title 登入
    el-input.login-input(v-model='loginForm.username', type='text', placeholder='Username')
    el-input.login-input(v-model='loginForm.password', type='password', placeholder='Password' @keyup.enter='handleLogin')
    .login-btn-container
        el-button(type='primary', @click='handleLogin') 登入
    strong 訪客測試登入
    div 帳號：user
    div 密碼：test12345
        //- el-button(@click='showCookie') show
        //- el-button(@click='removeCookie') Clear
</template>
<script>
import Cookies from 'js-cookie'
export default {
  data() {
    return{
      loginForm: {
        username: '',
        password: '',
        token: ''
      }
    }
  },
  created() {
    if (Cookies.get('login')) {
        this.$router.push('addCase')
    }
  },
  methods: {
    handleLogin() {
      this.$store.commit('SET_STATUS', 'pending')
      let username = this.loginForm.username
      let password = this.loginForm.password
      if (username !== '' && password !== '') {
        this.checkAuth()
      } else {
        alert('帳號密碼不能為空')
      }
    },
    checkAuth() {
        this.$http.post("/api/user/login",{email: this.loginForm.username, password: this.loginForm.password})
        .then(response => {
            console.log(response.data)
            this.loginForm.character = "manager"
            Cookies.set('login', JSON.stringify(this.loginForm), { expires: 30 })
            this.$store.commit('SET_STATUS', 'end')
            this.$router.push('addCase')
        })
        .catch((error) => {
            // console.log(error)
            this.$message.error(error.response.data)
            this.$store.commit('SET_STATUS', 'end')
        });
    },
    showCookie() {
        console.log(Cookies.get())
    },
    removeCookie() {
      Cookies.remove('login')
      console.log(Cookies.get())
    }
  }
}
</script>
